import { useMemo } from 'react';

export default function useIsAdmin<R extends { abbreviation: string }>(roles: Array<R>) {
  const isSuperAdmin = useMemo(
    () => !!roles.find((role) => role.abbreviation === 'SUPERADMIN'),
    [roles]
  );

  const isAdmin = useMemo(
    () =>
      !!roles.find(
        (role) =>
          role.abbreviation === 'SUPERADMIN' || role.abbreviation === 'ADMIN'
      ),
    [roles]
  );

  return { isSuperAdmin, isAdmin };
}

export function useIsCSS(roles: Array<{ abbreviation: string }>) {

  const isCSS = useMemo(
    () =>
      !!roles.find(
        (role) =>
          role.abbreviation === 'CSS'
      ),
    [roles]
  );

  return { isCSS };
}
